@import "../../default.css";

.container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    width: 70vw;
    height: 70vh;
}

.title {
    text-shadow: 0.1rem 0.1rem 0.1rem black;
}

.block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70%;
}

.column-header {
    background: rgb(39, 39, 39);
    box-shadow: 0.1rem 0.1rem 0.1rem black;
}

.row-header {
    background: rgb(39, 39, 39);
    box-shadow: 0.1rem 0.1rem 0.1rem black;
}

.bar {
    background: orangered;
    box-shadow: 0.1rem 0.1rem 0.1rem black;
}
